<template>
  <div class="home" id="home">
    <el-container>
      <el-main>
        <el-row>
          <el-col :span="4" style="border-right:1px solid #c6c8c8">
            <el-aside width="200px">
              <el-radio-group v-model="isCollapse" style="margin-bottom: 2px;">
                <el-radio-button :label="false">展开</el-radio-button>
                <el-radio-button :label="true">收起</el-radio-button>
              </el-radio-group>
              <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :router="true">
                <el-submenu index="homepage">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">主页</span>
                  </template>
                  <el-menu-item index="/home/system">系统首页</el-menu-item>
                </el-submenu>
                <el-menu-item index="/home/examine">
                  <i class="el-icon-menu"></i>
                  <span slot="title">审核</span>
                </el-menu-item>
                <el-menu-item index="/home/reportform">
                  <i class="el-icon-document"></i>
                  <span slot="title">报表</span>
                </el-menu-item>
                <el-menu-item index="/home/setting">
                  <i class="el-icon-setting"></i>
                  <span slot="title">设置</span>
                </el-menu-item>
              </el-menu>
            </el-aside>
          </el-col>
          <el-col :span="20" style="background-color: #fff">
            <el-breadcrumb separator="/" style="padding:20px;">
              <template v-for="route in routeList">
                <!--<span>{{!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}},</span>-->
                <el-breadcrumb-item :to="{path:!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}">{{route.name}}</el-breadcrumb-item>
              </template>
            </el-breadcrumb>
            <router-view></router-view>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {
        activeName: '',
        isCollapse: false,
        routeList:[]
      }
    },
    created(){
      this.$set(this,'routeList',this.$route.matched)
    },
    methods: {
      handleClick(tab, event) {

      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-header, .el-footer {
    /*background-color: #B3C0D1;*/
    color: #333;
    background-color: #3fbc9c;
    text-align: left;
  }

  .el-aside {
    overflow: inherit;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-button {
    height: 60px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-radio-group {
    display: inherit;
  }
</style>



